.Navbar {
  width: 30px;
  height: 100%;
  position: fixed;
  z-index: 100;
  transition: 0.5s ease-in-out;
  border-right: 1px solid rgba(255, 255, 255, 0.4);
  animation: navFade 2s linear forwards;
  opacity: 0;
  animation-delay: 1s;
}

.Navbar nav {
  height: 100%;
  display: flex;
  width: 40%;
  margin: auto;
}

.Navbar nav ul {
  display: flex;
  flex-direction: column;
  height: 50%;
  margin: auto;
  list-style: none;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.Navbar nav ul li {
  font-size: 1.6rem;
  cursor: pointer;
}

.Navbar nav ul li .Text--link {
  visibility: hidden;
  opacity: 0;
  display: none;
  color: white;
}

.Navbar nav ul li::before {
  content: "";
  width: 0;
  height: 1px;
  position: absolute;
  left: 0;
  display: block;
  background-color: white;
  transition: 0.1s ease-in-out;
}

.Navbar nav ul li::after {
  content: "";
  width: 0;
  height: 1px;
  position: absolute;
  right: 0;
  display: block;
  background-color: white;
  transition: 0.1s ease-in-out;
}

.Navbar nav ul li .fa-home,
.Navbar nav ul li .fa-briefcase,
.Navbar nav ul li .fa-envelope {
  visibility: visible;
  opacity: 1;
  color: white;
}

@keyframes navFade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.Navbar--active {
  width: 350px;
  box-shadow: 0 2px 10px 2px rgba(255, 255, 255, 0.4);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}

.Navbar--active nav ul li .Text--link {
  opacity: 1;
  visibility: visible;
  display: block;
}

.Navbar--active nav ul li:hover::before, .Navbar--active nav ul li:hover::after {
  width: 100%;
}

.Navbar--active nav ul li .fa-home,
.Navbar--active nav ul li .fa-briefcase,
.Navbar--active nav ul li .fa-envelope {
  visibility: hidden;
  opacity: 0;
  display: none;
}

@media screen and (max-width: 795px), screen and (max-device-width: 795px) {
  .Navbar {
    width: 100%;
    height: 50px;
    bottom: 0;
  }
  .Navbar nav {
    width: 100%;
  }
  .Navbar nav ul {
    padding: 0 20px;
    display: flex;
    flex-direction: row;
  }
  .Navbar--active nav ul li .Text--link {
    display: none;
  }
  .Navbar--active nav ul li:hover::before, .Navbar--active nav ul li:hover::after {
    width: 0;
  }
  .Navbar--active nav ul li .fa-home,
  .Navbar--active nav ul li .fa-briefcase,
  .Navbar--active nav ul li .fa-envelope {
    visibility: visible;
    opacity: 1;
    display: block;
  }
}
