  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Home .Intro--container .Info--container h1,
.Home .Intro--container .Info--container p {
  color: #f1f6f9;
  font-size: 4rem;
  font-weight: 700;
  margin: 0 10px;
  font-family: "Poppins", "Montserrat";
}

.Home .Intro--container .Info--container p {
  font-size: 2rem;
}

.Home .Intro--container .Info--container .Glitch {
  color: #f1f6f9;
  position: relative;
  animation: glitch 5s 5s infinite;
}

.Home .Intro--container .Info--container .Glitch::before {
  content: attr(datatext);
  position: absolute;
  left: -2px;
  text-shadow: -5px 0 #171f2a;
  background: transparent;
  overflow: hidden;
  top: 0;
  animation: noise-1 3s linear infinite alternate-reverse, glitch 5s 5.05s infinite;
}

.Home .Intro--container .Info--container .Glitch::after {
  content: attr(datatext);
  position: absolute;
  left: 2px;
  text-shadow: -5px 0 #171f2a;
  background: transparent;
  overflow: hidden;
  top: 0;
  animation: noise-2 3s linear infinite alternate-reverse, glitch 5s 5s infinite;
  width: 100%;
}

.Home .Mouse--indicator {
  width: 30px;
  height: 40px;
  position: absolute;
  top: 95%;
  left: 50%;
  transform: translate(-50%);
}

.Home .Mouse--indicator .Mouse--icon {
  left: 50%;
  transform: translate(-50%);
  position: absolute;
  width: 80%;
  height: 100%;
  border: 1px solid #f1f6f9;
  border-radius: 20px;
}

.Home .Mouse--indicator .Mouse--icon .Indicator {
  width: 5px;
  height: 9px;
  background-color: #f1f6f9;
  left: 50%;
  position: absolute;
  transform: translate(-50%);
  border-radius: 50%;
  animation: mouseAnim 1s linear infinite;
}

@keyframes mouseAnim {
  0% {
    transform: translateY(2px) translate(-50%);
  }
  50% {
    transform: translateY(5px) translate(-50%);
  }
  100% {
    transform: translateY(0px) translate(-50%);
  }
}

@keyframes glitch {
  1% {
    transform: rotateX(10deg) skewX(90deg);
  }
  2% {
    transform: rotateX(0deg) skewX(0deg);
  }
}

