.Sky .Dome {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  -webkit-clip-path: circle(50% at 50% 50%);
  clip-path: circle(50% at 50% 50%);
  border-radius: 50%;
}

.Sky .Dome .Still--star {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 2px;
  -webkit-filter: drop-shadow(0 0 6px #cad0db);
  filter: drop-shadow(0 0 6px #cad0db);
  transform: rotateZ(45deg);
}

